.flexible-col{
  -ms-flex:1 1 auto;
  flex:1 1 auto;
  word-wrap: break-word;
  max-width: 100%;
  &.text-right{
    text-align: right;
  };
  
  &.col-12{
    width:calc(~"4 / 8 * 100%");
  };
  
  &.col-9{
    width:calc(~"3 / 8 * 100%");
  };
}

.quantity{
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  min-width: 2em;
  background-color: @dark_cyan;
  color: @white;
  font-size: 16px;
  height:24px;
  padding-left: 0px !important;
  padding-right: 0px !important;
  border-radius: 12px;
  line-height: 24px;
  text-align: center;
}



.icon-left-container{
  -ms-flex:0 1 auto;
  flex:0 1 auto;
  max-width: 35px;
  text-align: center;
   span.cell__icon{
    padding-left: 5px!important;
  }
}
.icon-right-container{
  -ms-flex: 0 1 auto;
  flex: 0 1 auto;
  max-width:35px;
  text-align: center;
  >span {
    padding-right: 5px!important;
  }
}

.center-container{
  -ms-flex: 1 1 100%; 
  flex: 1 1 100%; 
  max-width:100%;
  padding: 0 5px!important;
}

.cell__primary {
    font-weight: bold;
}
.cell__uppercase {
    text-transform: uppercase;
}
.cell__font-md {
    font-size: 14px;
}
.cell__font-sm {
    font-size: 12px;
}
.cell__stroke {
    text-decoration: line-through;
}
.cell__text-right {
    text-align: right;
}
  


  .cell__icon {
    font-size: 25px;
    color: @green;
    cursor: pointer;
    &::before{
      vertical-align: middle;
    }
  }

.firstLine {
    overflow: hidden;
}

.secondLine {
    overflow: hidden;
}

.promo{
  min-width:37.5%;
  color:@green;
  text-transform: uppercase;
  font:bold 12px arial;
  line-height: 18px;
  padding-right:0px !important;
  cursor: pointer;
  .icon-arrow-right:before {
    text-align: right;
    vertical-align: middle;
    font-size:1em !important;
    font-weight: 900 !important;
  }
}
.promo-desc{
  color:@lightest_blue;
  letter-spacing: -0.1px;
  font:bold 12px arial;
  line-height: 16px;
  text-transform: capitalize;
}

.covenant, .covenant-desc{
text-transform: uppercase;
 font:bold 12px arial;
 line-height: 16px;
}

.covenant-colour{
  color: @violet;
}

